
.paginator {
    margin: 10px;
    cursor: pointer;
}
.pageNumber {
    padding: 2px;
   margin-right: 5px;
   color: gold;
   font-family: 'Baloo 2', cursive;
}

.pageNumber.selectedPage {
    font-weight: 900;
    color: white;
}
.button{
    color: white;
    padding: 4px 20px;
    font-family: 'Baloo 2', cursive;
    font-size: 10px;
    margin-top: 10px;
     border-radius: 10px;
     text-decoration: none;
     border: 1px solid rgb(216, 74, 173) ;
     background:none; 
}
.button:hover{
    background-color: rgb(216, 74, 173) ;
}